import React from "react";
import './Contact.css';
import gmailLogo from '../../assets/gmail.png'
const email = "amansaxena2002@gmail.com"

const handleEmailClick  = (e) =>
{
    navigator.clipboard.writeText(email);
}

const Contact = () => 
{
    return (
        <div className="contact-container">
            <div className="socials">
                <p className="title">Let's Talk!</p>
                <div className="email_container">
                    <img className="email_logo" src= {gmailLogo} alt="My Gmail"/>
                    <a className="myEmail" onClick={handleEmailClick}>
                        {email}
                    </a>
                </div>
            </div>
            <div className="message">
                <p>Send me a message</p>
                <div className="message_fields">
                    <input 
                    className="name_box text_field"
                    type="text"
                    placeholder="Your Name"
                    />
                    <input
                    className="text_field email_box"
                    type="email"
                    placeholder="Your email"
                    />
                    <input
                    className="message_box text_field"
                    type="text"
                    placeholder="Your message..."
                    />
                    <button className="submit">
                        Send
                    </button>
                </div>
                
            </div>
        </div>
    )
}

export default Contact;